import get from "lodash/get"
import { RpJsonDocument } from "@rp/react/rpjson/types"
import { DocumentsCollectionInfo } from "@rp/react/tanstack-api/documents"
import { getIsDocumentsCollectionInfoDocument } from "./getIsDocumentsCollectionInfoDocument"

const tabLabelDict: Record<string, string> = {
  FILING: "Report",
  TRANSCRIPT: "Transcript",
  "TRANSCRIPT-PRESENTATION": "Slides",
  "TRANSCRIPT-AUDIO": "Audio",
}

const tabLabelSubtypeDict: Record<string, string> = {
  "RNS-SEC-6-K": "Report 6-K",
  "RNS-SEC-8-K": "Report 8-K",
  "RNS-SEC-10-K": "Report 10-K",
  "RNS-SEC-10-Q": "Report 10-Q",
  "RNS-SEC-20-F": "Report 20-F",
  "RNS-SEC-S-1": "Report S-1",
  "RNS-SEC-S-3": "Report S-3",
}

const parseTabLabel = (doc: RpJsonDocument | DocumentsCollectionInfo | undefined): string => {
  const isDocumentsCollectionInfoDocument = getIsDocumentsCollectionInfoDocument(doc)

  const documentType: string | undefined = isDocumentsCollectionInfoDocument
    ? doc.rpDocumentType
    : get(doc, "document.metadata.custom.quartr_document_type")

  if (!documentType) {
    return "N/A"
  }

  if (documentType !== "FILING") {
    return tabLabelDict[documentType] ?? documentType
  }

  const documentSubtype: string | undefined =
    (isDocumentsCollectionInfoDocument
      ? doc.rpDocumentSubtype
      : get(doc, "document.metadata.custom.quartr_document_subtype")) ?? ""

  return tabLabelSubtypeDict[documentSubtype] || tabLabelDict.FILING
}

export { parseTabLabel }
